import axios from 'axios';

const getAllWoocommerce = (): Promise<Array<any>> => axios.get('productimp/v1/woocommerce/product/list')
  .then((response) => response.data.data)
  .catch((error) => {
    console.debug(error);
  });

const getWoocommerceProduct = (id: string): Promise<boolean> => axios.post('productimp/v1/woocommerce/product/read', { product_id: id })
  .then((response) => {
    console.log(response);
    return true;
  });

const createWoocommerceProduct = (id: string): Promise<boolean> => axios.post('productimp/v1/woocommerce/product/create', { product_id: id })
  .then((response) => {
    console.log(response);
    return true;
  });

export {
  getWoocommerceProduct,
  createWoocommerceProduct,
  getAllWoocommerce,
};
